import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import moment from 'moment';

Vue.use(Vuex);
export default new Vuex.Store({
  state: {
    spends: [],
    allCategories: [],
  },
  getters: {
    totalSpend: (state) => state.spends.reduce((prev, curr) => prev + curr.amount, 0),
    categories: (state) => [...state.allCategories, ...state.spends.map((x) => x.category)]
      .reduce((prev, curr) => [...prev, ...(prev.includes(curr) ? [] : [curr])], []),
    dayComparisionData: (state) => {
      const today = state.spends.reduce((prev, curr) => prev + (moment(curr.date).isSame(moment().startOf('day'), 'd') ? curr.amount : 0), 0);
      const yesterday = state.spends.reduce((prev, curr) => prev + (moment(curr.date).isSame(moment().subtract(1, 'days').startOf('day'), 'd') ? curr.amount : 0), 0);
      const yeyesterday = state.spends.reduce((prev, curr) => prev + (moment(curr.date).isSame(moment().subtract(2, 'days').startOf('day'), 'd') ? curr.amount : 0), 0);
      return {
        labels: ['Anteayer', 'Ayer', 'Hoy'],
        datasets: [
          {
            label: 'Gasto',
            backgroundColor: [
              'rgba(255, 99, 132, 1)',
              'rgba(54, 162, 235, 1)',
              'rgba(255, 206, 86, 1)',
            ],
            data: [yeyesterday, yesterday, today],
          },
        ]
      };
    },
  },
  mutations: {
    addSpend: (state, payload) => {
      const sortedArray = [...state.spends, payload].sort((a,b) => new moment(b.date).format('YYYYMMDD') - new moment(a.date).format('YYYYMMDD'));
      state.spends = sortedArray;
    },
    saveInitData: (state, payload) => {
      state.spends = payload;
    },
  },
  actions: {
    login: ({ commit },) => {
      return axios.get(
        'https://users.dcc.uchile.cl/~sblasco/wallet/')
        .then(({ data }) => {
          const sortedArray = data.sort((a,b) => new moment(b.date).format('YYYYMMDD') - new moment(a.date).format('YYYYMMDD'));
          commit('saveInitData', sortedArray);
          return Promise.resolve();
        });
    },
    addSpend: ({ state, commit }, payload) => {
      commit('addSpend', payload);
      return axios.post('https://users.dcc.uchile.cl/~sblasco/wallet/', state.spends)
        .then(({ data }) => {
          const sortedArray = data.sort((a,b) => new moment(b.date).format('YYYYMMDD') - new moment(a.date).format('YYYYMMDD'));
          commit('saveInitData', sortedArray);
          return Promise.resolve();
        });
    },
  },
  modules: {
  },
});
