<template>
  <div :class="$style.content">
    <h1>Pin</h1>
    <input
      :disabled="true"
      :class="$style.input"
      type="password"
      v-model="pin"/>
    <Keyboard v-model="pin"/>
    <br>
    <br>
    <button
      :disabled="loading"
      :class="$style.input"
      @click="access">Entrar</button>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import Keyboard from '@/components/Keyboard.vue';

export default {
  name: 'Pin',
  components: {
    Keyboard,
  },
  data() {
    return {
      pin: '',
      loading: false,
    };
  },
  methods: {
    ...mapActions(['login']),
    access() {
      this.loading = true;
      const payload = {
        pin: this.pin,
      };
      this.login(payload).then(() => {
        this.$router.push('/home');
      });
    },
  },
};
</script>

