<template>
  <div :class="$style.content">
    <h2>Agregar gasto</h2>
    <TotalSpend :amount="amount"/>
    <div :class="[$style.row, $style.categories]">
      <span
        :class="[$style.item, cat === category && $style.itemSelected]"
        v-for="cat in allCategories" :key="cat"
        @click="() => category = cat">{{ cat }}</span>
      <span
        :class="[$style.item]"
        @click="addOther">Otro</span>
    </div>
    <br>
    <input
      :class="$style.input"
      :disabled="loading"
      type="date"
      v-model="date"
      placeholder="DD/MM/YYYY"/>
    <br>
    <Keyboard v-model="amount"/>
    <br>
    <button
      :disabled="!ready || loading"
      :class="$style.input"
      @click="add">Agregar</button>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import moment from 'moment';

import Keyboard from '@/components/Keyboard.vue';
import TotalSpend from '@/components/TotalSpend.vue';

export default {
  name: 'Add',
  components: {
    Keyboard,
    TotalSpend,
  },
  computed: {
    ...mapGetters(['categories']),
    allCategories() {
      return [...this.categories, ...(this.category ? [this.category] : [])]
        .reduce((prev, curr) => [...prev, ...(prev.includes(curr) ? [] : [curr])], []);
    },
    ready() {
      return [
        this.amount,
        this.category,
        this.date,
      ].reduce((prev, curr) => prev && curr !== undefined, true);
    },
  },
  data() {
    return {
      loading: false,
      amount: '',
      category: undefined,
      date: moment().toISOString().split('T')[0],
    };
  },
  methods: {
    ...mapActions(['addSpend']),
    addOther() {
      /* eslint-disable */
      const newCategory = prompt('nombre');
      /* eslint-enable */
      this.category = newCategory;
    },
    add() {
      this.loading = true;
      const payload = {
        amount: parseInt(this.amount.replace('.', '').replace('$', ''), 10),
        category: this.category,
        date: this.date,
      };
      this.addSpend(payload).then(() => {
        this.loading = false;
        this.$router.push('/home');
      });
    },
  },
};
</script>

<style module lang="scss">
.content {
  display: flex;
  flex-direction: column;
}
.row {
  display: flex;
  flex-direction: row;
}
.item {
  display: flex;
  font-size: 1.25em;
  margin: 5px;
  color: white;
  background: rgb(9, 106, 185);
  opacity: 0.2;
  padding: 0 2px;
}
.itemSelected {
  box-shadow: 0 0 4px white;
  opacity: 1;
}
.input {
  width: 100%;
  font-size: 2em;
  height: 2em;
  margin: 0.1em 0;
}
.categories {
  flex-wrap: wrap;
  justify-content: center;
}
</style>
