<template>
  <div :class="$style.content">
    <div :class="$style.row">
      <div :class="$style.item" @click="input(`${value}1`)">1</div>
      <div :class="$style.item" @click="input(`${value}2`)">2</div>
      <div :class="$style.item" @click="input(`${value}3`)">3</div>
    </div>
    <div :class="$style.row">
      <div :class="$style.item" @click="input(`${value}4`)">4</div>
      <div :class="$style.item" @click="input(`${value}5`)">5</div>
      <div :class="$style.item" @click="input(`${value}6`)">6</div>
    </div>
    <div :class="$style.row">
      <div :class="$style.item" @click="input(`${value}7`)">7</div>
      <div :class="$style.item" @click="input(`${value}8`)">8</div>
      <div :class="$style.item" @click="input(`${value}9`)">9</div>
    </div>
    <div :class="$style.row">
      <div :class="$style.item" @click="input('')">🧹</div>
      <div :class="$style.item" @click="input(`${value}0`)">0</div>
      <div :class="$style.item" @click="input('')">🧹</div>
    </div>
  </div>
</template>

<script>
import beep from '@/assets/beep.mp3';

export default {
  name: 'Keyboard',
  props: {
    value: {
      type: String,
      required: true,
    },
  },
  methods: {
    input(val) {
      const audio = new Audio(beep);
      audio.play();
      this.$emit('input', val);
    },
  },
};
</script>

<style module lang="scss">
.content {
  font-size: 3em;
  display: flex;
  flex-direction: column;
}
.row {
  display: flex;
  flex-direction: row;
}
.item {
  display: flex;
  background: silver;
  flex: 1;
  text-align: center;
  justify-content: center;
  box-shadow: 0 0 2px gray;
  &:active {
    background: #a31dcc;
  }
}
</style>
