<script>
import { Bar, mixins } from 'vue-chartjs';
const { reactiveProp } = mixins;

export default {
  name: 'DayComparision',
  extends: Bar,
  mixins: [reactiveProp],
  data() {
    return {
      options: {
        responsive: true,
        maintainAspectRatio: false,
        legend: { display: false },
      },
    };
  },
  mounted () {
    this.renderChart(this.chartData, this.options);
  },
};
</script>

<style module lang="scss">
.content {
  font-size: 3em;
}
</style>
